const battles = require('./bd.json')
for(let s in battles)for(let t in battles[s]){
  cy.$id(s).length||cy.add({ group:'nodes', data: {id: s, name: 'Team '+nextID++ }, })
  cy.$id(t).length||cy.add({ group:'nodes', data: {id: t, name: 'Team '+nextID++ }, })
  cy.add({group:"edges",data:{target:t,source:s},});
  if(battles[s][t]>1) cy.add({group:"edges",data:{target:t,source:s},});
}
pageRank()
